.game {
  display: flex;
  margin-top: 5%;
  justify-content: center;
}

.game-board {
  margin-top: 3%;
  height: 30vh;
  width: 30vw;
  position: relative;
  float: right;
}

.square {
  width: 7vw;
  height: 9vh;
  margin: 0% 1% 0% 1%;
  font-size: 2.5em;
  font-weight: 500;
}

.row {
  height: 10vh;
}

.player-status {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.8em;
  font-weight: 700;
  color: #22272c;
  text-decoration: underline;
}

.player-moves {
  width: 20%;
  display: flex;
  flex-direction: column;
  margin-right: 5%;
  margin-left: 5%;
}
